
.label {
  margin: 0 2px;
  display: inline-block;
  white-space: nowrap;
}
.filters span.selected {
  font-weight: bold;
}
.repositories .status .glyphicon-ok {
  color: #080;
}
.repositories .status .glyphicon-remove {
  color: #800;
}
.repositories .tools button {
  margin-right: 1px;
  padding: 0px 5px;
}
.repositories .protected-branches span:after {
  content: ", ";
}
.repositories .protected-branches span:last-child:after {
  content: "";
}



